.root {
  height: 100%;
  display: flex;
  flex-direction: column;
  color: var(--color-main-text-80);
  background: var(--color-main);
  font-weight: var(--font-weight-medium);
}

.stepsList {
  display: flex;
  padding: 0 1rem 0 0.5rem;
  height: 1rem;
  align-items: center;
  font-size: medium;
}

.stepsItem {
  height: 0.25rem;
  background: var(--color-main-text-20);
  flex: auto;
  margin-left: 0.5rem;
  border-radius: 0.125rem;
}

.item_active {
  background: var(--color-main-text-80);
}

.title {
  font-size: var(--font-size-36);
  padding: 1rem 1rem 0 2rem;
}

.subtitle {
  font-size: var(--font-size-22);
  padding: 1rem 1rem 0 2rem;
}

.data {
  font-size: var(--font-size-22);
  padding: 1rem 1rem 0 2rem;
  flex-grow: 1;
}

.item {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.time {
  font-size: var(--font-size-14);
  border-radius: 50%;
  border: solid 2px var(--color-main-text-80);
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin-right: 1rem;
  padding-bottom: 1px;
  flex: none;
}

.repeatCount {
  width: 2rem;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  display: flex;
}

.repeatItem {
  width: 0.5rem;
  height: 0.5rem;
  background: var(--color-main-text-20);
  margin-right: 0.5rem;
  border-radius: 50%;
  flex: none;
}

.prevNext {
  display: flex;
  padding: 2rem;
  justify-content: space-between;
}
